/* src/Styles/Main.css */

.background-container {
  position: relative;
  width: 100vw;
  height: 100vh;
  display: flex;
  background-image: url("../Images/bg.png");
  background-size: cover;
  background-position: center;
  overflow: hidden;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(40px);
  -webkit-backdrop-filter: blur(8px);
  z-index: 1;
}

.nav {
  position: absolute;
  top: 30px;
  left: 50%;
  transform: translateX(-50%);
  font-family: "SF Pro", sans-serif;
  font-weight: 700;
  font-size: 28px;
  text-align: center;
  color: white;
  z-index: 2;
}

.posts-container {
  z-index: 2;
  position: absolute;
  top: 100px; 
  left: 520px;/* –°–¥–≤–∏–≥–∞–µ–º –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä –Ω–µ–º–Ω–æ–≥–æ –≤–ª–µ–≤–æ –¥–ª—è –æ–ø—Ç–∏–º–∏–∑–∞—Ü–∏–∏ */
  width: 50%; /* –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º —à–∏—Ä–∏–Ω—É –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞ —Å –ø–æ—Å—Ç–∞–º–∏ */
  max-height: calc(100vh - 150px); /* –û–≥—Ä–∞–Ω–∏—á–∏–≤–∞–µ–º –ø–æ –≤—ã—Å–æ—Ç–µ, —á—Ç–æ–±—ã —Å–∫—Ä–æ–ª–ª –Ω–µ –º–µ—à–∞–ª */
  
  display: flex;
  flex-direction: column;
  gap: 20px; /* –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º –ø—Ä–æ–º–µ–∂—É—Ç–æ–∫ –º–µ–∂–¥—É –ø–æ—Å—Ç–∞–º–∏ */
  align-items: flex-start; /* –í—ã—Ä–∞–≤–Ω–∏–≤–∞–µ–º –ø–æ—Å—Ç—ã –ø–æ –ª–µ–≤–æ–º—É –∫—Ä–∞—é */

  overflow-y: scroll;
  overflow-x: hidden;
}

/* –û–±—â–∏–π –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä —Å–∫—Ä–æ–ª–ª–±–∞—Ä–∞ */
::-webkit-scrollbar {
  width: 8px; /* –∏–ª–∏ height: 8px –¥–ª—è –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω–æ–≥–æ */
}

/* –¢—Ä–µ–∫ (—Ñ–æ–Ω —Å–∫—Ä–æ–ª–ª–±–∞—Ä–∞) */
::-webkit-scrollbar-track {
  background: #f0f0f0;
  border-radius: 10px;
}

/* –ü–æ–ª–∑—É–Ω–æ–∫ */
::-webkit-scrollbar-thumb {
  background-color: #888;
  border-radius: 10px;
  border: 2px solid #f0f0f0; /* –≤–æ–∫—Ä—É–≥ –ø–æ–ª–∑—É–Ω–∫–∞ */
}

/* –ü—Ä–∏ –Ω–∞–≤–µ–¥–µ–Ω–∏–∏ */
::-webkit-scrollbar-thumb:hover {
  background-color: #555;
}


.post-card {
  background-color: rgba(255, 255, 255, 0.1);
  color: white;
  border-radius: 16px;
  padding: 30px; /* –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º –æ—Ç—Å—Ç—É–ø—ã –¥–ª—è –ø–æ—Å—Ç–∞ */
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.25);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
  width: 100%; /* –®–∏—Ä–∏–Ω–∞ –ø–æ—Å—Ç–æ–≤ –±—É–¥–µ—Ç –∞–¥–∞–ø—Ç–∏—Ä–æ–≤–∞—Ç—å—Å—è –ø–æ —Ä–∞–∑–º–µ—Ä—É –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞ */
}

.post-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 25px rgba(0, 0, 0, 0.4);
}

.post-author {
  font-weight: bold;
  font-size: 1.3em; /* –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º —Ä–∞–∑–º–µ—Ä —à—Ä–∏—Ñ—Ç–∞ –¥–ª—è –∏–º–µ–Ω–∏ –∞–≤—Ç–æ—Ä–∞ */
}

.post-date {
  font-size: 1.1em;
  color: #bbb;
  margin-bottom: 10px;
}

.post-content {
  font-size: 1.2em; /* –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º —Ä–∞–∑–º–µ—Ä —à—Ä–∏—Ñ—Ç–∞ –¥–ª—è –∫–æ–Ω—Ç–µ–Ω—Ç–∞ */
  line-height: 1.8; /* –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º –º–µ–∂—Å—Ç—Ä–æ—á–Ω—ã–π –∏–Ω—Ç–µ—Ä–≤–∞–ª */
}

/* üîΩ –ê–¥–∞–ø—Ç–∏–≤–Ω–æ—Å—Ç—å */
@media (max-width: 1024px) {
  .posts-container {
    width: 50%; /* –£–º–µ–Ω—å—à–∞–µ–º —à–∏—Ä–∏–Ω—É –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞ –¥–ª—è —ç–∫—Ä–∞–Ω–æ–≤ –º–µ–Ω–µ–µ 1024px */
  }
}

@media (max-width: 768px) {
  .posts-container {
    width: 80%; /* –®–∏—Ä–∏–Ω–∞ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞ –¥–ª—è —ç–∫—Ä–∞–Ω–æ–≤ –º–µ–Ω–µ–µ 768px */
  }

  .nav {
    font-size: 24px;
  }

  .post-card {
    padding: 20px; /* –ú–µ–Ω—å—à–µ padding –Ω–∞ –º–∞–ª–µ–Ω—å–∫–∏—Ö —ç–∫—Ä–∞–Ω–∞—Ö */
  }
}

@media (max-width: 480px) {
  .posts-container {
    width: 95%; /* –ü–æ—á—Ç–∏ –≤–µ—Å—å —ç–∫—Ä–∞–Ω –¥–ª—è –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞ —Å –ø–æ—Å—Ç–∞–º–∏ –Ω–∞ –º–æ–±–∏–ª—å–Ω—ã—Ö */
  }

  .nav {
    font-size: 20px; /* –£–º–µ–Ω—å—à–∞–µ–º —Ä–∞–∑–º–µ—Ä —à—Ä–∏—Ñ—Ç–∞ –¥–ª—è –∑–∞–≥–æ–ª–æ–≤–∫–∞ */
    top: 20px;
  }

  .post-card {
    padding: 16px; /* –ú–µ–Ω—å—à–µ padding –¥–ª—è –ø–æ—Å—Ç–æ–≤ –Ω–∞ –º–æ–±–∏–ª—å–Ω—ã—Ö */
  }

  .post-author {
    font-size: 1em;
  }

  .post-content {
    font-size: 1em;
  }
}
/* –§–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω—ã–π —Å–∫—Ä–æ–ª–ª–±–∞—Ä */
.fixed-scrollbar {
  position: fixed;
  top: 0;
  right: 0;
  width: 8px;
  height: 100%;
  background-color: rgba(249, 243, 243, 0.97);
  z-index: 999;
  border-radius: 10px;
}

