/* src/Styles/PostCard.css */

.post-card {
    background-color: rgba(255, 255, 255, 0.1);
    color: white;
    border-radius: 16px;
    padding: 30px; /* –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º –æ—Ç—Å—Ç—É–ø—ã –¥–ª—è –±–æ–ª—å—à–µ–≥–æ —Ä–∞–∑–º–µ—Ä–∞ */
    backdrop-filter: blur(10px);
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.25);
    transition: transform 0.2s ease, box-shadow 0.2s ease;
    height: auto; /* –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –¥–∏–Ω–∞–º–∏—á–µ—Å–∫—É—é –≤—ã—Å–æ—Ç—É */
  }
  
  .post-card:hover {
    transform: translateY(-2px);
    box-shadow: 0 6px 25px rgba(0, 0, 0, 0.4);
  }
  
  .post-header {
    display: flex;
    align-items: center;
    gap: 12px;
    margin-bottom: 20px;
  }
  
  .post-header img {
    border-radius: 50%;
    width: 50px;
    height: 50px;
  }
  
  .post-author {
    font-weight: bold;
    font-size: 1.3em; /* –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º —Ä–∞–∑–º–µ—Ä —à—Ä–∏—Ñ—Ç–∞ */
  }
  
  .post-date {
    font-size: 1.1em;
    color: #bbb;
  }
  
  .post-content {
    font-size: 1.2em; /* –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º —Ä–∞–∑–º–µ—Ä —à—Ä–∏—Ñ—Ç–∞ */
    line-height: 1.8; /* –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º –º–µ–∂—Å—Ç—Ä–æ—á–Ω—ã–π –∏–Ω—Ç–µ—Ä–≤–∞–ª */
  }
  
  .post-actions {
    display: flex;
    gap: 15px;
    margin-top: 15px;
  }
  
  .post-actions svg {
    cursor: pointer;
    transition: color 0.3s ease;
  }
  
  .post-actions svg:hover {
    color: #1da1f2; /* –°–∏–Ω–∏–π —Ü–≤–µ—Ç –¥–ª—è –∞–∫—Ç–∏–≤–Ω—ã—Ö –∏–∫–æ–Ω–æ–∫ */
  }
  
  .post-actions .like-icon,
  .post-actions .comment-icon,
  .post-actions .share-icon {
    width: 20px;
    height: 20px;
  }
  /* –°—Ç–∏–ª–∏ –¥–ª—è –Ω–æ–≤—ã—Ö –∫–Ω–æ–ø–æ–∫ */
.post-actions-buttons {
  display: flex;
  gap: 10px;
  margin-top: 10px;
}

.edit-btn,
.delete-btn {
  background-color: transparent;
  border: 1px solid #ccc;
  color: #ccc;
  padding: 5px 10px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 1em;
  transition: all 0.3s ease;
}

.edit-btn:hover,
.delete-btn:hover {
  background-color: #1da1f2;
  color: white;
  border-color: #1da1f2;
}
