/* Comments.css */

/* –°—Ç–∏–ª–∏ –¥–ª—è –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞ –∫–æ–º–º–µ–Ω—Ç–∞—Ä–∏–µ–≤ */
.comments-section {
    margin-top: 20px;
  }
  
  /* –°—Ç–∏–ª–∏ –¥–ª—è –∫–∞–∂–¥–æ–≥–æ –∫–æ–º–º–µ–Ω—Ç–∞—Ä–∏—è */
  .comment {
    border: 2px solid #ccc; /* –†–∞–º–∫–∞ */
    padding: 10px; /* –û—Ç—Å—Ç—É–ø—ã –≤–Ω—É—Ç—Ä–∏ —Ä–∞–º–∫–∏ */
    margin: 10px 0; /* –û—Ç—Å—Ç—É–ø—ã –º–µ–∂–¥—É –∫–æ–º–º–µ–Ω—Ç–∞—Ä–∏—è–º–∏ */
    width: 500px; /* –§–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω–∞—è —à–∏—Ä–∏–Ω–∞ */
    max-width: 100%; /* –ú–∞–∫—Å–∏–º–∞–ª—å–Ω–∞—è —à–∏—Ä–∏–Ω–∞ –¥–ª—è –∞–¥–∞–ø—Ç–∏–≤–Ω–æ—Å—Ç–∏ */
    box-sizing: border-box; /* –£—á–∏—Ç—ã–≤–∞–µ–º padding –∏ border –≤ —à–∏—Ä–∏–Ω–µ */
    border-radius: 5px; /* –û–∫—Ä—É–≥–ª—ã–µ —É–≥–ª—ã */
    background-color: #f9f9f9; /* –¶–≤–µ—Ç —Ñ–æ–Ω–∞ */
  }
  
  /* –°—Ç–∏–ª–∏ –¥–ª—è –∞–≤—Ç–æ—Ä–æ–≤ –∫–æ–º–º–µ–Ω—Ç–∞—Ä–∏–µ–≤ */
  .comment-author {
    display: flex; /* –†–∞—Å–ø–æ–ª–∞–≥–∞–µ–º –∏–º—è –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è –∏ –∫–Ω–æ–ø–∫–∏ –≤ –æ–¥–Ω—É —Å—Ç—Ä–æ–∫—É */
    justify-content: space-between; /* –†–∞—Å–ø—Ä–µ–¥–µ–ª—è–µ–º —ç–ª–µ–º–µ–Ω—Ç—ã —Å –æ—Ç—Å—Ç—É–ø–∞–º–∏ */
    font-weight: bold;
    margin-bottom: 5px;
    font-size: 14px;
    color: #0056b3;
    width: 100%; /* –®–∏—Ä–∏–Ω–∞ –¥–æ–ª–∂–Ω–∞ –±—ã—Ç—å –¥–æ—Å—Ç–∞—Ç–æ—á–Ω–æ–π –¥–ª—è —Ä–∞–∑–º–µ—â–µ–Ω–∏—è —Ç–µ–∫—Å—Ç–∞ –∏ –∫–Ω–æ–ø–æ–∫ */
  }
  
  /* –°—Ç–∏–ª–∏ –¥–ª—è —Å–æ–¥–µ—Ä–∂–∏–º–æ–≥–æ –∫–æ–º–º–µ–Ω—Ç–∞—Ä–∏—è */
  .comment-content {
    margin-bottom: 10px;
    font-size: 14px;
    color: #333;
  }
  
  /* –°—Ç–∏–ª–∏ –¥–ª—è –¥–∞—Ç—ã –∫–æ–º–º–µ–Ω—Ç–∞—Ä–∏—è */
  .comment-date {
    font-size: 12px;
    color: #888;
  }
  
  /* –°—Ç–∏–ª–∏ –¥–ª—è –∫–Ω–æ–ø–∫–∏ –¥–æ–±–∞–≤–ª–µ–Ω–∏—è –∫–æ–º–º–µ–Ω—Ç–∞—Ä–∏—è */
  .add-comment textarea {
    width: 100%;
    height: 100px;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #ccc;
    resize: none;
    font-size: 14px;
  }
  
  .add-comment button {
    padding: 10px 15px;
    margin-top: 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .add-comment button:hover {
    background-color: #0056b3;
  }
  
  /* –°—Ç–∏–ª–∏ –¥–ª—è –±–ª–æ–∫–∞ —Å –∫–Ω–æ–ø–∫–∞–º–∏ —Ä–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –∏ —É–¥–∞–ª–µ–Ω–∏—è */
  .comment-actions {
    display: flex;
    gap: 10px; /* –û—Ç—Å—Ç—É–ø—ã –º–µ–∂–¥—É –∫–Ω–æ–ø–∫–∞–º–∏ */
  }
  
  /* –°—Ç–∏–ª–∏ –¥–ª—è –∫–Ω–æ–ø–æ–∫ —Ä–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏—è –∏ —É–¥–∞–ª–µ–Ω–∏—è */
  .comment-actions button {
    padding: 5px 10px;
    font-size: 12px; /* –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º —Ä–∞–∑–º–µ—Ä —à—Ä–∏—Ñ—Ç–∞ —Ç–∞–∫–æ–π –∂–µ, –∫–∞–∫ —É –∏–º–µ–Ω–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—è */
    cursor: pointer;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #acadb3;
    display: inline-flex;
    align-items: center;
  }
  
  .comment-actions button:hover {
    background-color: #007bff;
    color: #fff;
  }
  